/*
 (Alternative Image)
Padding-toppadding-bottomd
*/
.alt-img,
.sprite {
  @include sprite-img;
}

.alt-img {
  overflow: hidden;
}

// 

.block-link {
  color: $base-text-color;
  cursor: pointer;

  &:hover {
    text-decoration: none;

    .block-link__link {
      text-decoration: underline;
    }
  }

  .block-link__link {
    color: $base-link-color;
  }
}

// 
.cancel-child-margin {

  @include cancel-child-margin;
}
